.App {
  text-align: center;
}

#headline {
  height: 150px;
  background-image: cover;
}

input {
  background: rgb(1,102,52);
  color: white;
  margin: 0 5px;
  outline: none;
  height: 25px;
}

select {
  margin: 0 5px;
  height: 25px;
}

ul {
  list-style: none;
  text-align: left;
  padding-left: 5px;
  margin: 0;
}

ul li {
  padding: none;
}

table {
  text-align: center;
  margin: 0 auto;
}

thead {
  background: rgb(1,102,52);
  color: white;
  border: 1px solid black;
}

th{
  padding: 0 15px;
}

.table-body-row:hover {
  background: rgba(34,122,78, 0.2);
}

#player-card{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin: 5px 0;
}

#player-pic{
  height: 150px;
  background-image: contain;
}


